body {
  background-color: rebeccapurple;
}
.synthMain {
  justify-content: center;
  display: grid;
}
.synthBorder {
  background-color: aqua;
  border-radius: 50em;
  width: 50rem;
  height: auto;
}
h1 {
  display: flex;
  justify-content: center;
}
button {
  margin-top: 8px;
  width: 100px;
  border: 1px solid black;
  height: 20px;
  text-align: center;
  appearance: none;
  background-color: white;
  float: left;
  margin-left: 2em;
}
.waveTypes {
  display: flex;
  justify-content: space-evenly;
}
.ctrls {
  display: flex;
  flex-direction: row;
  margin-top: 2em;
  justify-content: space-evenly;
}

/*Synth-Sequencer*/

.sequencerMain {
  justify-content: center;
  display: grid;
  margin-top: 5rem;
}
